"use client";

import React, { useState, useEffect, useCallback } from 'react';
import { useParams } from 'next/navigation';
import { GoogleMap, LoadScript, Marker, InfoWindow } from '@react-google-maps/api';
import Link from 'next/link';

// Define the vehicle data interface
interface Vehicle {
  vehicle_id: string;
  route_id: string;
  current_status: string;
  latitude: number;
  longitude: number;
  speed: number;
  bearing: number;
  timestamp: string;
  [key: string]: any; // Allow for additional properties
}

// Define the route data interface
interface Route {
  route_id: string;
  route_long_name: string;
  route_desc: string;
  route_color: string;
  route_stops: any[];
}

export default function LiveRouteMap() {
  const params = useParams();
  const routeId = params.routeid as string;
  
  const [vehicles, setVehicles] = useState<Vehicle[]>([]);
  const [route, setRoute] = useState<Route | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [selectedVehicle, setSelectedVehicle] = useState<Vehicle | null>(null);
  const [mapCenter, setMapCenter] = useState({ lat: 33.7175, lng: -117.8311 }); // Default to OC center

  // Fetch route details
  const fetchRouteDetails = useCallback(async () => {
    try {
      const response = await fetch(`/api/routes/${routeId}`);
      if (!response.ok) {
        throw new Error(`Failed to fetch route details: ${response.status}`);
      }
      const data = await response.json();
      setRoute(data);
      
      // If we have stops, center the map on the first stop
      if (data.route_stops && data.route_stops.length > 0) {
        setMapCenter({
          lat: parseFloat(data.route_stops[0].stop_lat),
          lng: parseFloat(data.route_stops[0].stop_lon)
        });
      }
    } catch (err) {
      console.error('Error fetching route details:', err);
      setError('Failed to load route details');
    }
  }, [routeId]);

  // Fetch vehicle positions
  const fetchVehiclePositions = useCallback(async () => {
    try {
      const response = await fetch(`http://127.0.0.1:5000/api/vehicle-positions/${routeId}`);
      if (!response.ok) {
        throw new Error(`Failed to fetch vehicle positions: ${response.status}`);
      }
      const data = await response.json();
      
      // Filter vehicles to only include those for this route
      const routeVehicles = (data.vehicles || []).filter(
        (vehicle: Vehicle) => vehicle.route_id === routeId
      );
      
      setVehicles(routeVehicles);
      
      // If we have vehicles, center the map on the first vehicle
      if (routeVehicles.length > 0) {
        setMapCenter({
          lat: routeVehicles[0].latitude,
          lng: routeVehicles[0].longitude
        });
      }
    } catch (err) {
      console.error('Error fetching vehicle positions:', err);
      setError('Failed to load vehicle positions');
    } finally {
      setLoading(false);
    }
  }, [routeId]);

  // Initial data fetch
  useEffect(() => {
    fetchRouteDetails();
    fetchVehiclePositions();
    
    // Set up polling every 10 seconds
    const intervalId = setInterval(fetchVehiclePositions, 10000);
    
    // Clean up interval on component unmount
    return () => clearInterval(intervalId);
  }, [fetchRouteDetails, fetchVehiclePositions]);

  // Map container style
  const mapContainerStyle = {
    width: '100%',
    height: '600px'
  };

  // Format timestamp to readable date/time
  const formatTimestamp = (timestamp: string) => {
    if (!timestamp) return 'N/A';
    const date = new Date(parseInt(timestamp) * 1000);
    return date.toLocaleString();
  };

  return (
    <div className="p-6 max-w-6xl mx-auto">
      <div className="flex justify-between items-center mb-6">
        <h1 className="text-2xl font-bold">
          {route ? route.route_long_name : `Route ${routeId}`} - Live Map
        </h1>
        <Link 
          href={`/route/${routeId}`}
          className="bg-blue-500 hover:bg-blue-600 text-white px-4 py-2 rounded"
        >
          Back to Route Details
        </Link>
      </div>
      
      {error && (
        <div className="bg-red-100 border border-red-400 text-red-700 px-4 py-3 rounded mb-4">
          <p>Error: {error}</p>
        </div>
      )}
      
      {loading ? (
        <div className="text-center py-8">
          <p>Loading live bus data...</p>
        </div>
      ) : (
        <div>
          <div className="mb-4">
            <p className="text-lg">
              <span className="font-medium">Active Buses:</span> {vehicles.length}
            </p>
            <p className="text-sm text-gray-600">
              Last updated: {new Date().toLocaleTimeString()}
            </p>
          </div>
          
          <div className="mb-6">
            <LoadScript googleMapsApiKey={process.env.NEXT_PUBLIC_GOOGLE_MAPS_API_KEY || ''}>
              <GoogleMap
                mapContainerStyle={mapContainerStyle}
                center={mapCenter}
                zoom={13}
                options={{
                  styles: [
                    {
                      featureType: "poi",
                      elementType: "labels",
                      stylers: [{ visibility: "off" }]
                    }
                  ]
                }}
              >
                {/* Display vehicles as markers */}
                {vehicles.map((vehicle, index) => (
                  <Marker
                    key={index}
                    position={{
                      lat: vehicle.latitude,
                      lng: vehicle.longitude
                    }}
                    icon={{
                      path: google.maps.SymbolPath.CIRCLE,
                      scale: 10,
                      fillColor: route?.route_color || '#4285F4',
                      fillOpacity: 0.8,
                      strokeColor: '#FFFFFF',
                      strokeWeight: 2
                    }}
                    onClick={() => setSelectedVehicle(vehicle)}
                  />
                ))}
                
                {/* Info window for selected vehicle */}
                {selectedVehicle && (
                  <InfoWindow
                    position={{
                      lat: selectedVehicle.latitude,
                      lng: selectedVehicle.longitude
                    }}
                    onCloseClick={() => setSelectedVehicle(null)}
                  >
                    <div className="p-2">
                      <h3 className="font-bold">Bus {selectedVehicle.vehicle_id || 'Unknown'}</h3>
                      <p>Route: {selectedVehicle.route_id}</p>
                      <p>Status: {selectedVehicle.current_status || 'N/A'}</p>
                      <p>Speed: {selectedVehicle.speed ? `${selectedVehicle.speed} mph` : 'N/A'}</p>
                      <p>Updated: {formatTimestamp(selectedVehicle.timestamp)}</p>
                    </div>
                  </InfoWindow>
                )}
              </GoogleMap>
            </LoadScript>
          </div>
          
          <div className="mt-6">
            <h2 className="text-xl font-semibold mb-2">Active Buses</h2>
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
              {vehicles.map((vehicle, index) => (
                <div 
                  key={index} 
                  className="border rounded p-4 bg-white shadow cursor-pointer hover:bg-gray-50"
                  onClick={() => setSelectedVehicle(vehicle)}
                >
                  <h4 className="font-bold mb-2">Bus {vehicle.vehicle_id || 'Unknown'}</h4>
                  <div className="grid grid-cols-2 gap-2 text-sm">
                    <div>
                      <span className="font-medium">Status:</span> {vehicle.current_status || 'N/A'}
                    </div>
                    <div>
                      <span className="font-medium">Speed:</span> {vehicle.speed ? `${vehicle.speed} mph` : 'N/A'}
                    </div>
                    <div>
                      <span className="font-medium">Updated:</span> {formatTimestamp(vehicle.timestamp)}
                    </div>
                  </div>
                </div>
              ))}
            </div>
            
            {vehicles.length === 0 && (
              <div className="text-center py-8 bg-gray-50 rounded">
                <p>No active buses found for this route.</p>
                <p className="text-sm text-gray-600 mt-2">
                  This could mean the route is not currently in service or there are no vehicles reporting their position.
                </p>
              </div>
            )}
          </div>
        </div>
      )}
    </div>
  );
} 